module.exports = "<h1 id=react-redux-starter-kit>React Redux Starter Kit</h1> <p><a href=\"https://travis-ci.org/davezuko/react-redux-starter-kit?branch=master\"><img src=\"https://travis-ci.org/davezuko/react-redux-starter-kit.svg?branch=master\" alt=\"Build Status\"></a> <a href=https://david-dm.org/davezuko/react-redux-starter-kit><img src=https://david-dm.org/davezuko/react-redux-starter-kit.svg alt=dependencies></a> <a href=\"https://david-dm.org/davezuko/react-redux-starter-kit#info=devDependencies\"><img src=https://david-dm.org/davezuko/react-redux-starter-kit/dev-status.svg alt=\"devDependency Status\"></a> <a href=http://standardjs.com/ ><img src=https://img.shields.io/badge/code%20style-standard-brightgreen.svg alt=js-standard-style></a></p> <p>This starter kit is designed to get you up and running with a bunch of awesome front-end technologies.</p> <p>The primary goal of this project is to provide a stable foundation upon which to build modern web appliications. Its purpose is not to dictate your project structure or to demonstrate a complete real-world application, but to provide a set of tools intended to make front-end development robust, easy, and, most importantly, fun. Check out the full feature list below!</p> <p>Finally, This project wouldn&#39;t be possible without the help of our many contributors. What you see today is the product of hundreds changes made to keep up with an ever-evolving ecosystem. <a href=#thank-you>Thank you</a> for all of your help.</p> <h2 id=table-of-contents>Table of Contents</h2> <ol> <li><a href=#requirements>Requirements</a></li> <li><a href=#getting-started>Installation</a></li> <li><a href=#running-the-project>Running the Project</a></li> <li><a href=#project-structure>Project Structure</a></li> <li><a href=#local-development>Live Development</a><ul> <li><a href=#hot-reloading>Hot Reloading</a></li> <li><a href=#redux-devtools>Redux DevTools</a></li> </ul> </li> <li><a href=#routing>Routing</a></li> <li><a href=#testing>Testing</a><ul> <li><a href=#dirty-chai>dirty-chai</a></li> </ul> </li> <li><a href=#building-for-production>Building for Production</a></li> <li><a href=#deployment>Deployment</a></li> <li><a href=#thank-you>Thank You</a></li> </ol> <h2 id=requirements>Requirements</h2> <ul> <li>node <code>^5.0.0</code></li> <li>yarn <code>^0.23.0</code> or npm <code>^3.0.0</code></li> </ul> <h2 id=installation>Installation</h2> <p>After confirming that your environment meets the above <a href=#requirements>requirements</a>, you can create a new project based on <code>react-redux-starter-kit</code> by doing the following:</p> <pre><code class=lang-bash>$ git clone https://github.com/davezuko/react-redux-starter-kit.git &lt;my-project-name&gt;\n$ cd &lt;my-project-name&gt;\n</code></pre> <p>When that&#39;s done, install the project dependencies. It is recommended that you use <a href=https://yarnpkg.com/ >Yarn</a> for deterministic dependency management, but <code>npm install</code> will suffice.</p> <pre><code class=lang-bash>$ yarn  # Install project dependencies (or `npm install`)\n</code></pre> <h2 id=running-the-project>Running the Project</h2> <p>After completing the <a href=#installation>installation</a> step, you&#39;re ready to start the project!</p> <pre><code class=lang-bash>$ yarn start  # Start the development server (or `npm start`)\n</code></pre> <p>While developing, you will probably rely mostly on <code>yarn start</code>; however, there are additional scripts at your disposal:</p> <table> <thead> <tr> <th><code>yarn &lt;script&gt;</code></th> <th>Description</th> </tr> </thead> <tbody> <tr> <td><code>start</code></td> <td>Serves your app at <code>localhost:3000</code></td> </tr> <tr> <td><code>build</code></td> <td>Builds the application to ./dist</td> </tr> <tr> <td><code>test</code></td> <td>Runs unit tests with Karma. See <a href=#testing>testing</a></td> </tr> <tr> <td><code>test:watch</code></td> <td>Runs <code>test</code> in watch mode to re-run tests when changed</td> </tr> <tr> <td><code>lint</code></td> <td><a href=http://stackoverflow.com/questions/8503559/what-is-linting>Lints</a> the project for potential errors</td> </tr> <tr> <td><code>lint:fix</code></td> <td>Lints the project and <a href=http://eslint.org/docs/user-guide/command-line-interface.html#fix>fixes all correctable errors</a></td> </tr> </tbody> </table> <h2 id=project-structure>Project Structure</h2> <p>The project structure presented in this boilerplate is <strong>fractal</strong>, where functionality is grouped primarily by feature rather than file type. This structure is only meant to serve as a guide, it is by no means prescriptive. That said, it aims to represent generally accepted guidelines and patterns for building scalable applications. If you wish to read more about this pattern, please check out this <a href=https://github.com/davezuko/react-redux-starter-kit/wiki/Fractal-Project-Structure>awesome writeup</a> by <a href=https://github.com/justingreenberg>Justin Greenberg</a>.</p> <pre><code>.\n├── build                    # All build-related code\n├── public                   # Static public assets (not imported anywhere in source code)\n├── server                   # Express application that provides webpack middleware\n│   └── main.js              # Server application entry point\n├── src                      # Application source code\n│   ├── index.html           # Main HTML page container for app\n│   ├── main.js              # Application bootstrap and rendering\n│   ├── normalize.js         # Browser normalization and polyfills\n│   ├── components           # Global Reusable Components\n│   ├── containers           # Global Reusable Container Components\n│   ├── layouts              # Components that dictate major page structure\n│   │   └── PageLayout       # Global application layout in which to render routes\n│   ├── routes               # Main route definitions and async split points\n│   │   ├── index.js         # Bootstrap main application routes with store\n│   │   ├── Home             # Fractal route\n│   │   │   ├── index.js     # Route definitions and async split points\n│   │   │   ├── assets       # Assets required to render components\n│   │   │   ├── components   # Presentational React Components\n│   │   │   └── routes **    # Fractal sub-routes (** optional)\n│   │   └── Counter          # Fractal route\n│   │       ├── index.js     # Counter route definition\n│   │       ├── container    # Connect components to actions and store\n│   │       ├── modules      # Collections of reducers/constants/actions\n│   │       └── routes **    # Fractal sub-routes (** optional)\n│   ├── store                # Redux-specific pieces\n│   │   ├── createStore.js   # Create and instrument redux store\n│   │   └── reducers.js      # Reducer registry and injection\n│   └── styles               # Application-wide styles (generally settings)\n└── tests                    # Unit tests\n</code></pre><h2 id=live-development>Live Development</h2> <h3 id=hot-reloading>Hot Reloading</h3> <p>Hot reloading is enabled by default when the application is running in development mode (<code>yarn start</code>). This feature is implemented with webpack&#39;s <a href=https://webpack.github.io/docs/hot-module-replacement.html>Hot Module Replacement</a> capabilities, where code updates can be injected to the application while it&#39;s running, no full reload required. Here&#39;s how it works:</p> <ul> <li><p>For <strong>JavaScript</strong> modules, a code change will trigger the application to re-render from the top of the tree. <strong>Global state is preserved (i.e. redux), but any local component state is reset</strong>. This differs from React Hot Loader, but we&#39;ve found that performing a full re-render helps avoid subtle bugs caused by RHL patching.</p> </li> <li><p>For <strong>Sass</strong>, any change will update the styles in realtime, no additional configuration or reload needed.</p> </li> </ul> <h3 id=redux-devtools>Redux DevTools</h3> <p><strong>We recommend using the <a href=https://chrome.google.com/webstore/detail/redux-devtools/lmhkpmbekcpmknklioeibfkpmmfibljd>Redux DevTools Chrome Extension</a>.</strong> Using the chrome extension allows your monitors to run on a separate thread and affords better performance and functionality. It comes with several of the most popular monitors, is easy to configure, filters actions, and doesn&#39;t require installing any packages in your project.</p> <p>However, it&#39;s easy to bundle these developer tools locally should you choose to do so. First, grab the packages from npm:</p> <pre><code class=lang-bash>yarn add --dev redux-devtools redux-devtools-log-monitor redux-devtools-dock-monitor\n</code></pre> <p>Then follow the <a href=https://github.com/gaearon/redux-devtools/blob/master/docs/Walkthrough.md>manual integration walkthrough</a>.</p> <h2 id=routing>Routing</h2> <p>We use <code>react-router</code> <a href=https://github.com/ReactTraining/react-router/blob/v3/docs/API.md#plainroute>route definitions</a> (<code>&lt;route&gt;/index.js</code>) to define units of logic within our application. See the <a href=#project-structure>project structure</a> section for more information.</p> <h2 id=testing>Testing</h2> <p>To add a unit test, create a <code>.spec.js</code> file anywhere inside of <code>./tests</code>. Karma and webpack will automatically find these files, and Mocha and Chai will be available within your test without the need to import them. Here are a few important plugins and packages available to you during testing:</p> <h3 id=dirty-chai>dirty-chai</h3> <p>Some of the assertions available from <a href=chaijs.com>chai</a> use <a href=http://chaijs.com/api/bdd/#method_true>magical getters</a>. These are problematic for a few reasons:</p> <p>1) If you mistype a property name (e.g. <code>expect(false).to.be.tru</code>) then the expression evaluates to undefined, the magical getter on the <code>true</code> is never run, and so your test silently passes. 2) By default, linters don&#39;t understand them and therefore mark them as unused expressions, which can be annoying.</p> <p><a href=https://github.com/prodatakey/dirty-chai>Dirty Chai</a> fixes this by converting these getters into callable functions. This way, if mistype an assertion, our attempt to invoke it will throw due to the property being undefined.</p> <pre><code class=lang-js>// This silently passes because the getter on `true` is never invoked!\nit(&#39;should be true&#39;, () =&gt; {\n  expect(false).to.be.tru // evalutes to undefined :(\n})\n\n// Much better! Our assertion is invalid, so it throws rather than implicitly passing.\nit(&#39;should be true&#39;, () =&gt; {\n  expect(false).to.be.tru() // `tru` is not defined!\n})\n</code></pre> <h2 id=building-for-production>Building for Production</h2> <h2 id=deployment>Deployment</h2> <p>Out of the box, this starter kit is deployable by serving the <code>./dist</code> folder generated by <code>yarn build</code>. This project does not concern itself with the details of server-side rendering or API structure, since that demands a more opinionated structure that makes it difficult to extend the starter kit. The simplest deployment strategy is a <a href=#static-deployments>static deployment</a>.</p> <h3 id=static-deployments>Static Deployments</h3> <p>Serve the application with a web server such as nginx by pointing it at your <code>./dist</code> folder. Make sure to direct incoming route requests to the root <code>./dist/index.html</code> file so that the client application will be loaded; react-router will take care of the rest. If you are unsure of how to do this, you might find <a href=https://github.com/reactjs/react-router/blob/master/docs/guides/Histories.md#configuring-your-server>this documentation</a> helpful. The Express server that comes with the starter kit is able to be extended to serve as an API and more, but is not required for a static deployment.</p> <h2 id=thank-you>Thank You</h2> <p>This project wouldn&#39;t be possible without help from the community, so I&#39;d like to highlight some of its biggest contributors. Thank you all for your hard work, you&#39;ve made my life a lot easier and taught me a lot in the process.</p> <ul> <li><a href=https://github.com/justingreenberg>Justin Greenberg</a> - For all of your PR&#39;s, getting us to Babel 6, and constant work improving our patterns.</li> <li><a href=https://github.com/neverfox>Roman Pearah</a> - For your bug reports, help in triaging issues, and PR contributions.</li> <li><a href=https://github.com/SpencerCDixon>Spencer Dixon</a> - For your creation of <a href=https://github.com/SpencerCDixon/redux-cli>redux-cli</a>.</li> <li><a href=https://github.com/mtsr>Jonas Matser</a> - For your help in triaging issues and unending support in our Gitter channel.</li> </ul> <p>And to everyone else who has contributed, even if you are not listed here your work is appreciated.</p> ";

