module.exports = "<h1 id=markdown-loader>markdown-loader</h1> <p>markdown-loader for webpack using <a href=https://github.com/chjj/marked>marked</a>.</p> <p><a href=https://www.npmjs.com/package/markdown-loader><img src=https://img.shields.io/npm/v/markdown-loader.svg alt=\"\"></a> <a href=https://www.npmjs.com/package/markdown-loader><img src=https://img.shields.io/npm/dm/markdown-loader.svg alt=\"\"></a> <a href=https://david-dm.org/peerigon/markdown-loader><img src=https://david-dm.org/peerigon/markdown-loader.svg alt=\"Dependency Status\"></a> <a href=https://travis-ci.org/peerigon/markdown-loader><img src=\"https://travis-ci.org/peerigon/markdown-loader.svg?branch=master\" alt=\"Build Status\"></a></p> <h2 id=installation>Installation</h2> <p><code>npm install markdown-loader</code></p> <h2 id=usage>Usage</h2> <p>Since marked&#39;s output is HTML, it&#39;s best served in conjunction with the <a href=https://github.com/webpack/html-loader>html-loader</a>.</p> <h3 id=webpack-2>Webpack 2</h3> <pre><code class=lang-javascript>{\n    module: {\n        rules: [{\n                test: /\\.md$/,\n                use: [\n                    {\n                        loader: &quot;html-loader&quot;\n                    },\n                    {\n                        loader: &quot;markdown-loader&quot;,\n                        options: {\n                            /* your options here */\n                        }\n                    }\n                ]\n            }]\n    }\n}\n</code></pre> <h3 id=options>Options</h3> <p>Simply pass your marked <a href=https://github.com/chjj/marked#options-1>options</a> as shown above. In order to specify <a href=https://github.com/peerigon/markdown-loader/issues/5>custom renderers</a>, simply set the <code>options.renderer</code>-option on your webpack options.</p> <pre><code class=lang-javascript>// webpack.config.js\n\nconst marked = require(&quot;marked&quot;);\nconst renderer = new marked.Renderer();\n\nreturn {\n    module: {\n        rules: [{\n                test: /\\.md$/,\n                use: [\n                    {\n                        loader: &quot;html-loader&quot;\n                    },\n                    {\n                        loader: &quot;markdown-loader&quot;,\n                        options: {\n                            pedantic: true,\n                            renderer\n                        }\n                    }\n                ]\n            }]\n    }\n}\n</code></pre> <h2 id=license>License</h2> <p>MIT (<a href=http://www.opensource.org/licenses/mit-license.php>http://www.opensource.org/licenses/mit-license.php</a>)</p> <h2 id=sponsors>Sponsors</h2> <p><a href=https://peerigon.com><img src=https://assets.peerigon.com/peerigon/logo/peerigon-logo-flat-spinat.png width=150 /></a></p> ";

